<template>
    <div class="feedback-layout">
        <header class="feedback-header">
            <a @click="goPrePage()" class="feedback-cancle">取消</a>
            <h2 class="feedback-title">意见反馈</h2>
            <button  class="feedback-send" id="feedback-send" :disabled="!isAble" @click="sendMsg()">发送</button>
        </header>
        <div class="feedback-content-box">
            <textarea class="feedback-content" placeholder="写评论......" name="content" id="feedback-area" rows="10" cols="50" v-model="feedContent">
            </textarea>
        </div>
    </div>
</template>
<script>
    import autoSize from 'autosize';
    import Api from '../../../api/api';
    import {sendMessage} from '../../../api/suggestfeed';
    import scriptFilter from '../../../utils/filterScript';
    export default{
        data(){
            return {
                isAble:false,
                feedContent:'',
            }
        },
        mounted(){
            /**
             * 获得焦点,改变样式
             */
            autoSize(document.querySelectorAll('textarea'));
            const contentBox = $('#feedback-area');
            contentBox.focus();
            /**
             * 获得焦点,改变样式
             */
            const feedBtn = $('#feedback-send');
            const _this = this;
            _this.changeBtnBg(_this,contentBox,feedBtn);

        },
        methods:{
            showMsg(str){
            this.$toast({
              message: str,
              duration: 2000,
            });
          },
            sendMsg(){
                const _this = this;
                if(scriptFilter(this.feedContent)){
                    this.showMsg('反馈内容不能包含奇怪字符!');
                    return;
                }
                if(this.feedContent.length<20){
                    this.showMsg('反馈内容不低于20个字哦');
                }else{
                    sendMessage(Api.SUGGESTBACK,_this.feedContent).then((res)=>{
                        //跳转到反馈记录界面
                      if(res.status ===200){
//                        _this.$router.push({path: '/psh'});
                        this.$router.replace('/psh');
                      }
                    },(err)=>{
                        _this.$store.dispatch('hideLoading');
                        _this.showMsg('反馈信息提交失败,请重试!');
                    });
                }
            },
            /**
             * 返回上一级元素
             */
            goPrePage(){
                window.history.go(-1);
            },
            setBackGround(ele,bg,color,border) {
                ele.css({
                    'background':bg,
                    'color':color,
                    'border':border
                })
            },
            /**
             * 根据输入内容设置发送按钮的样式
             * @param _this
             * @param contentBox:内容dom元素
             * @param feedBtn:按钮元素
             */
            changeBtnBg(_this,contentBox,feedBtn){
                $(document).on('input propertychange', 'textarea', function() {
                    //function code
                    _this.feedContent = contentBox.val();
                    if(_this.feedContent!=='null' &&_this.feedContent.length>0){
                        _this.isAble = true;
                        _this.setBackGround(feedBtn,'#20a0ff','#ffffff','1px solid #20a0ff');
                    }else{
                        _this.isAble = false;
                        _this.setBackGround(feedBtn,'#ffffff','#9c9c9c','1px solid #9c9c9c');
                    }
                });
            }
        }
    }
</script>
<style scoped>
    @import "../../../assets/css/feedback.css";
    body {
        background-color: #ffffff;
    }
</style>



