import axios from 'axios';
/**
 * 处理反馈信息
 * @param url
 * @param suggest_content
 * @returns {*}
 */
export function sendMessage(url,suggest_content) {
  return axios.post(url,{
    suggest_content:suggest_content
  }).then((res)=>{
    return Promise.resolve(res);
  },(err)=>{
    return Promise.reject(err);
  })
}
/**
 * 查看历史记录
 * @param url
 * @returns {*}
 */
export function suggestHistoryInfo(url) {
  return axios.get(url).then((res)=>{
    return Promise.resolve(res);
  },(err)=>{
    return Promise.reject(err);
  });
}



